import uCharts from '../../components/u-charts/u-charts.js'
var _self;
var canvaLineA = null;
var mqtt = require('../../common/js/mqtt.min.js')
export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			showCharts: false,
			time_range: ['08:25', '09:45', '10:22', '12:12', '13:25', '16:25', '18:25'],
			dateList: [{
					date: '2020年2月19日',
					time: ['09:55:16', '10:25:26', '09:55:16']
				},
				{
					date: '2020年2月20日',
					time: ['09:55:16', '10:25:26', '09:55:16', '09:55:16']
				},
			],
			bpm: '86BPM',

			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			client: null,
			patientList: [],
			patient: null,
			heart_rate_list: [],
			categories: [],
			date: currentDate,
			mac: '',
			typeList: [{
					time: '5分钟',
					type: "1"
				},
				{
					time: '10分钟',
					type: "2"
				},
				{
					time: '30分钟',
					type: "3"
				},
				{
					time: '60分钟',
					type: "4"
				},
			],
			timeType: '5分钟',
			type: '1',
			dataList: [],
			highest_bpm: 0,
			lowest_bpm: 0,
			avg_bpm: 0,
			showPopup: true, // 是否显示弹窗
		};
	},
	onLoad(options) {
		_self = this;
		if (options.pid) {
			// 进页面有患者id就执行
			this.fetchPatientInfo(options.pid)
			this.showPopup = false
		} else {
			// 默认第一个患者
			this.fetchPatientInfo(1)
		}
		this.fetchPatientList()
		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
	},
	onHide() {
		this.showCharts = false
		this.heart_rate_list = []
		this.categories = []
	},
	computed: {
		startDate() {
			return this.getDate('start')
		},
		endDate() {
			return this.getDate('end')
		}
	},
	watch: {
		patient(newVal, oldVal) {
			this.drawChart()
		},
		type(newVal, oldVal) {
			if (this.patient) {
				this.drawChart()
			}
		},
		date(newVal, oldVal) {
			if (this.patient) {
				this.drawChart()
			}
		},
	},

	methods: {
		close() {
			this.showPopup = false
		},
		toChoice() {
			uni.redirectTo({
				url: `../choicePatient/choicePatient?id=2`,
			})
		},
		drawChart() {
			this.heart_rate_list = []
			this.categories = []
			this.getData()
		},
		// 选择患者picker
		// bindPickerChange(e) {
		// 	for (let item of this.patientList) {
		// 		if (item.id === Number(e.detail.value) + 1) {
		// 			this.fetchPatientInfo(item.id)
		// 		}
		// 	}
		// },
		// 选择时间段picker
		bindTypeChange(e) {
			for (let item of this.typeList) {
				if (this.typeList.indexOf(item) == e.detail.value) {
					this.timeType = item.time
					this.type = item.type
					return
				}
			}
		},

		async getData() {
			this.showCharts = true
			await uni.request({
				url: 'https://ciaiky.le-cx.com/php/chaxun.php',
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				data: {
					shangchuan_date: this.date,
					data_mingzi: this.patient.mac,
					data_sj: this.type
					// shangchuan_date: "2020-01-01",
					// data_mingzi: this.patient.mac,
					// data_sj: "1"
				},
				success: res => {
					this.dataList = res.data
					for (let item of this.dataList) {
						this.heart_rate_list.push(item.data_xl)
						this.categories.push(item.data_time)
					}
					this.highest_bpm = Math.max(...this.heart_rate_list)
					this.lowest_bpm = Math.min(...this.heart_rate_list)
					this.avg_bpm = this.getAverage(this.heart_rate_list)
					_self.showLineA("myChart", this.categories, this.heart_rate_list)
				}
			})
		},
		// 获取患者列表(ID)
		async fetchPatientList() {
			await uni.request({
				url: 'https://ciai.le-cx.com/index.php/api/patient/patientList',
				success: res => {
					this.patientList = res.data.data
				}
			})
		},
		// 获取患者信息
		async fetchPatientInfo(id) {
			await uni.request({
				url: `https://ciai.le-cx.com/index.php/api/patient/info?id=${id}`,
				success: res => {
					this.patient = res.data.data
				},
			})
		},

		showLineA(canvasId, categories, heart_rate) {
			// 图表实例和配置
			canvaLineA = new uCharts({
				$this: _self,
				canvasId: canvasId,
				colors: ['#FFFFFF'],
				type: 'line',
				fontSize: 12,
				dataLabel: false,
				dataPointShape: false,
				background: '#24C789',
				pixelRatio: _self.pixelRatio,
				categories: categories,
				series: [{
					name: '实时心率',
					data: heart_rate
				}],
				animation: false,
				xAxis: {
					gridColor: '#FFF',
					gridType: 'dash',
					disableGrid: true,
					axisLine: false,
					fontColor: '#FFF',
					disabled: true,
					// boundaryGap: 'justify'
				},
				yAxis: {
					data: [{
						position: 'right',
						fontColor: '#FFF',
						axisLineColor: '#24C789',
					}],
					gridType: 'dash',
					gridColor: '#FFF',
					splitNumber: 5,
					dashLength: 2,
				},
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					line: {
						type: 'straight'
					},
					tooltip: {
						gridType: 'dash',
						dashLength: 5,
						gridColor: '#24C789'
					}
				}
			});
		},

		touchLineA(e) {
			canvaLineA.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		},
		// 日期选择器
		bindDateChange(e) {
			this.date = e.target.value
		},

		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		// 求数组平均值
		getAverage(arr) {
			var sum = 0
			for (let i = 0; i < arr.length; i++) {
				sum += parseInt(arr[i])
			}
			return Math.ceil(sum / arr.length)
		}
	},
}
